/* eslint-disable import/prefer-default-export */
/* eslint-disable import/order */
/* eslint-disable wrap-iife */
import Vue from 'vue'
import '@badam/vue-tsx-support/enable-check'
import '@badam/vue-tsx-support/options/enable-nativeon'
import {
  calculateWindow,
  exec,
  ready,
  addWebpAvifSupport,
  initErrorReporter,
  reportSensors,
  metric,
  DayJs,
  TimeType,
  getPlatform,
  setLang,
  getServTs,
  appType,
  updateTsDelta,
  calculateWindow4,
} from '@badam/common-utils'
import store from './store'
import { getArgs, initAppleLogin, pkg } from './utils'
import { name, nanoid } from '../../package.json'
import './index.module.scss'
import lang from './lang/index.ksa.json'
import Loading from './components/loading.vue'
import { getPkgClientId } from '../src_unrg/utils'

export function mainInit() {
  Vue.config.productionTip = false
  Vue.component('LoadingComp', Loading)

  if (getArgs('lan') !== 'zh') {
    setLang(lang)
  } else {
    setLang({}, '', true)
  }

  async function setTime() {
    const time = await getServTs(appType.sawa)
    updateTsDelta(time)
  }

  function init() {
    return Promise.all([setTime()])
  }

  function createGoogleSDKScript() {
    return new Promise((r) => {
      const loadGoogleApi = () => {
        store.state.googleLoad = true
      }
      const script = document.createElement('script')
      script.type = 'text/javascript'
      script.src = 'https://accounts.google.com/gsi/client'
      script.onload = loadGoogleApi
      document.body.appendChild(script)
    })
  }

  function initBtn() {
    ;(function (d, s, id) {
      let js: any
      let fjs: any = d.getElementsByTagName(s)[0]
      if (d.getElementById(id)) {
        return
      }
      js = d.createElement(s)
      js.id = id
      js.src = 'https://connect.facebook.net/en_US/sdk.js'
      if (fjs) fjs.parentNode.insertBefore(js, fjs)
    })(document, 'script', 'facebook-jssdk')
    ;(window as any).fbAsyncInit = function () {
      const appid = getPkgClientId(pkg).facebook
      console.log('facebook初始化 包名', pkg, appid)
      const v = 'v6.0'
      ;(window as any).FB.init({
        appId: appid,
        xfbml: true,
        version: v,
      })
      ;(window as any).FB.AppEvents.logPageView()
    }
  }

  createGoogleSDKScript()
  initBtn()

  exec(async () => {
    store.state.data = decodeURIComponent(getArgs('data')) || ''
    initAppleLogin()
    calculateWindow4()
    banScale()

    try {
      init()
      DayJs.setOffset(TimeType.Ksa)
      reportSensors('act_meaningful_content_loaded', true)
    } catch (err) {
      console.error(err)
    }
  })

  function banScale() {
    document.documentElement.addEventListener(
      'touchstart',
      (event) => {
        if (event.touches.length > 1) {
          event.preventDefault()
        }
      },
      false
    )

    let lastTouchEnd = 0
    document.documentElement.addEventListener(
      'touchend',
      (event) => {
        const now = Date.now()
        if (now - lastTouchEnd <= 300) {
          event.preventDefault()
        }
        lastTouchEnd = now
      },
      false
    )
    document.addEventListener('gesturestart', (event) => {
      event.preventDefault()
    })
  }
}
