import Vue from 'vue'
import { getArgsFromUrl, getArgsFromUrlHash, matchExpr } from '@badam/common-utils'
import Qs from 'qs'
import { PageEnum, resolvePage, writePageToLS } from './utils'
import router from './router'
import s1 from './src_unrg/store'
import s2 from './src_bind/store'
import m1 from './src_unrg/main.vue'
import m2 from './src_bind/main.vue'
import { mainInit as unrgInit } from './src_unrg/init'
import { mainInit as bindInit } from './src_bind/init'
import { initDarkMode, initSawaSDK, resolveLudoStyle, loadVConsoleIfNeed } from './utils/func'
import './src_bind/assets/css/ludo.scss'
import { getConfigAndDial } from './utils/dial'
import FirebaseManager from './utils/firebase-manager'
import { resolveApiBase, setDefApiBase } from './src_unrg/api'

async function main() {
  const p = resolvePage()
  console.warn('resolvePage', p)
  writePageToLS(p)
  resolveLudoStyle()
  initSawaSDK()
  initDarkMode()
  // @Td FirebaseManager域名替换，生产放开
  setDefApiBase()
  if ((process as any).env.VUE_APP_FLAVOR2 !== 'weyay') {
    try {
      const base = resolveApiBase()
      // 测试环境不走替换
      if (base.includes('test')) {
        throw new Error('skip replace')
      }
      if (base.indexOf('arab-pk-release') < 0) {
        await FirebaseManager.getInstance().init()
        await getConfigAndDial()
      }
    } catch (err) {
      console.error(err)
    }
  } else {
    // let base = resolveApiBase()
    // if (base.indexOf('api-release.weyay.chat') >= 0) {
    //   base = base.replace('api-release.weyay.chat', 'api.weyay.chat')
    //   setDefApiBase(base)
    // }
  }
  const store = matchExpr(p, [
    [PageEnum.Unregister, () => s1],
    [PageEnum.BindMobile, () => s2],
    [PageEnum.UnbindMobile, () => s2],
    [PageEnum.UpdateMobile, () => s2],
  ])
  const Main = matchExpr(p, [
    [PageEnum.Unregister, () => m1],
    [PageEnum.BindMobile, () => m2],
    [PageEnum.UnbindMobile, () => m2],
    [PageEnum.UpdateMobile, () => m2],
  ])

  const q = Qs.parse(location.hash)
  const query: any = {}
  for (let k in q) {
    if (k.indexOf('#/') >= 0) {
      const key = k.replace('#/', '')
      query[key] = q[k]
    } else {
      query[k] = q[k]
    }
  }
  console.log('location.href', location.href, location.hash, query)

  p === PageEnum.Unregister ? unrgInit() : bindInit()
  if ([PageEnum.BindMobile, PageEnum.UnbindMobile, PageEnum.UpdateMobile].includes(p)) {
    router.push({ name: 'BindHome', query })
  }

  if (p === PageEnum.Unregister) {
    router.push({ name: 'Home', query })
  }

  loadVConsoleIfNeed()

  new Vue({
    store,
    router,
    render: (h) => h(Main),
  }).$mount('#app')
}

;(window as any).router = router

main()

