import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import { getArgsFromUrl } from '@badam/common-utils'
import UnrgHome from './src_unrg/views/home/index.vue'
import UnrgDwnHome from './src_unrg/views/home/index.dwn.vue'
import BindHome from './src_bind/views/home/index.vue'

const Home = getArgsFromUrl('bundle_id') === 'hk.longchiu.dwn' ? UnrgDwnHome : UnrgHome

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  /// 注销功能 路由
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/mobile',
    name: 'Mobile',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ './src_unrg/views/mobile/mobile.vue'),
  },
  {
    path: '/selectCountry',
    name: 'SelectCountry',
    component: () => import('./src_unrg/views/mobile/country-select.vue'),
  },
  /// 0417新增，绑定功能 路由
  {
    path: '/bind_home',
    name: 'BindHome',
    component: BindHome,
  },
  {
    path: '/bind_mobile',
    name: 'BindMobile',
    component: () => import('./src_bind/views/mobile/mobile.vue'),
  },
  {
    path: '/bind_selectCountry',
    name: 'BindSelectCountry',
    component: () => import('./src_bind/views/mobile/country-select.vue'),
  },
]

console.log('init kan zenlayer')

const router = new VueRouter({
  routes,
})

export default router
