/* eslint-disable import/prefer-default-export */
import { ref } from 'vue'

export const showCA = ref(false)

export const resolve = ref()

export function startCA() {
  showCA.value = true
  return new Promise((r) => {
    resolve.value = r
  })
}

export function endCA() {
  showCA.value = false
  if (resolve.value) resolve.value(true)
}
