function getiOSVersion(): number {
  let userAgent = navigator.userAgent
  if (/iPad|iPhone|iPod/.test(userAgent)) {
    // 匹配 iOS 设备
    let match = userAgent.match(/OS (\d+)_(\d+)_?(\d+)?/)
    if (match !== null) {
      // 获取版本号并转换为数字
      let version = parseInt(match[1], 10)
      return version
    }
  }
  // 非 iOS 设备或者获取版本失败时返回 null
  return 14
}

