async function onPressApple() {
  try {
    loading.value = true
    const data = await AppleID.auth.signIn()
    console.log('data', data)
    if (!data || !data.authorization || !data.authorization.id_token) {
      reportEvent(Events.account_cancellation_verify_page_operation_result, {
        operation: '验证',
        result: '失败',
      })
      loading.value = false
      return
    }

    const { code: apple_uid, id_token: apple_token } = data.authorization
    const user_id = getSub(apple_token)
    if (channel.value && channel.value !== 'hk.longchiu.dwn.h5') {
      doVerify(VerifyType.apple, {
        user_id,
        apple_code: apple_uid,
        channel: channel.value,
        access_token: apple_uid,
        channel_values: apple_token,
      })
    } else {
      doVerify(VerifyType.apple, { user_id, access_token: apple_token, apple_code: apple_uid })
    }
  } catch (error) {
    //
    loading.value = false
  }
}

function attachSignin() {
  const { google } = window as any

  if (google) {
    google.accounts.id.initialize({
      client_id: CLIENT_ID,
      callback: (data: any) => {
        console.log('google data', data)
        doVerify(VerifyType.google, { id_token: data?.credential })
      },
    })
    google.accounts.id.renderButton(document.getElementById('google-login-btn'), {
      theme: 'outline',
      size: 'large',
      type: 'standard',
      width: '311',
    })
  }
}

